.container__trainers {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
}

.container__trainers {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
}

.trainer__img {
    border-radius: 0 2rem;
    overflow: hidden;
}

.trainer h3 {
    margin-top: 2rem;
}

.trainer  {
    margin-top: 0.85rem;
}

.trainer__socials {
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
}

.trainer__socials a {
    font-size: 0.7rem;
    background: var(--color-gray-600);
    padding: 0.8rem;
    border-radius: 0.7rem;
    transition: var(--transition);
}

.trainer:hover a {
    background: var(--color-gray-500);
}


/* MEDIA QUERIES(medium screen) */

@media screen and (max-width : 1024px) {
    .container__trainers {
        grid-template-columns: 1fr 1fr;
        gap: 2.5rem;
    }
}

@media screen and (max-width : 600px) {
    .container__trainers {
        grid-template-columns: 1fr;
        gap: 2rem;
    }

    .card.trainer {
        width: 90%;
        margin: 0 auto;
    }
}